					"html": "<p><code>external</code>, or \"FFI\" (foreign function interface), or simply \"interop\" is how Reason/OCaml communicates with other languages, like C or JavaScript. It's like mandatorily typing a let-binding.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"hljs lang-reason\"><code>external myCFunction: int -&gt; string = <span class=\"hljs-string\">\"theCFunction\"</span>;</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"hljs lang-reason\"><code>external getElementsByClassName : string =&gt; array <span class=\"hljs-type\">Dom</span>.element =\n  <span class=\"hljs-string\">\"document.getElementsByClassName\"</span> [@@bs.<span class=\"hljs-keyword\">val</span>];</code></pre>\n      </div>\n<p>You'd often see <code>external</code>s when working with BuckleScript, since we're interoping with existing JavaScript libraries a lot. More info on BS externals <a href=\"http://bucklescript.github.io/bucklescript/Manual.html#_binding_to_simple_js_functions_values\">here</a>.</p>",
